.Backdrop {
  display: none;
}

@media only screen and (max-width: 480px) {
  .Backdrop {
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: 1000;
    left: 0;
    top: 0;
    background-color: rgba(0, 0, 0, 0.5);
  }

  .OpenBackdrop {
    display: block;
  }

  .CloseBackdrop {
    display: none;
  }
}
