$--default-border: #c3cfdd;
$--color-primary: #1b3a57;
$darkestBlue: #051e34;
$lightestBlue: #1580de;
$middleOrange: #ffa000;
$mintGreen: #46d4b4;

.Customer {
  border: 0.5px solid $--default-border;
  box-shadow: 0 1px 2px 0 rgba(60, 64, 67, 0.3);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  background-color: rgb(255, 255, 255);
  width: 100%;
  height: 100%;
  overflow: auto;
  padding-bottom: 0.8rem;
}

.Customer:hover {
  h3 {
    color: white;
  }

  .BorderUnderline {
    transform: translateX(0%);
  }
}

.CustomerTitle {
  background-image: linear-gradient(#1b3a57ed, $--color-primary),
    url(http://JelenaStrbac.github.io/customer-manager/images/entrepreneur.jpg);
  background-size: contain;
  color: white;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.CustomerTitle h3 {
  padding: 0.7rem 0;
  font-weight: 400;
  color: rgba(255, 255, 255, 0.7);
  text-align: center;
}

.CustomerIcons {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  width: 100%;
}

.CustomerIcon {
  width: 100%;
  height: 100%;
  background-color: rgba(221, 221, 221, 0.05);
  display: flex;
  justify-content: center;
  align-items: center;
  -webkit-clip-path: polygon(0 40%, 100% 0, 100% 100%, 0% 100%);
          clip-path: polygon(0 40%, 100% 0, 100% 100%, 0% 100%);
  box-shadow: 10px -5px 1px 1px rgba(221, 221, 221, 0.15);
  border-right: solid 1px rgba(221, 221, 221, 0.15);
  padding: 1rem;
  color: rgba(255, 255, 255, 0.7);
  cursor: pointer;
  font-size: 0.9rem;
  &:hover {
    background-color: rgba(221, 221, 221, 0.1);
  }
  i {
    font-size: 1.1rem;
  }
}

.Active {
  background-color: white;
  color: $lightestBlue;
  border: none;
  transition: all 0.3s ease;
  &:hover {
    background-color: white;
  }
}

.Border {
  width: 100%;
  height: 0.5rem;
  padding-bottom: 0.2rem;
  display: flex;
}

.BorderOne {
  background-color: $middleOrange;
  width: 100%;
}
.BorderTwo {
  background-color: $mintGreen;
  width: 100%;
}
.BorderThree {
  background-color: $lightestBlue;
  width: 100%;
}

.BorderUnderline {
  background-image: linear-gradient(
    to right,
    $middleOrange,
    $mintGreen,
    $lightestBlue
  );
  height: 0.1rem;
  width: 100%;
  transform: translateX(-100%);
  transition: transform 0.5s ease;
}

.CustomerData {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  .Button {
    font-size: 0.8rem;
    padding: 0.8rem 1.6rem;
  }
}

.CustomerContent {
  padding: 1rem 1rem 0rem 1rem;
  width: 100%;
  div {
    color: rgba(0, 0, 0, 0.6);
    font-size: 0.8rem;
    padding-bottom: 0.3rem;
    overflow-wrap: anywhere;
    width: 100%;
    word-wrap: break-word;
    b {
      color: rgba(0, 0, 0, 0.6);
      font-size: 0.8rem;
      font-weight: 400;
    }
  }
}
